import React, { useState } from 'react';
import { Container, Form, Button, Card, Alert } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import { useAuth } from '../contexts/AuthContext'; 
import './Login.css'; 

function Login() {
  const [username, setUsername] = useState(''); 
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const navigate = useNavigate();
  const { login } = useAuth(); 

  const handleSubmit = async (e) => { 
    e.preventDefault(); 
    setError(''); 

    try {
      const response = await axios.post('/api/login', { username, password });
      
      //  { id, name }   AuthContext login  
      if (response.data.id) {
        // AuthContext login     
        login(response.data); 
        //    localStorage id  
        localStorage.setItem('userId', response.data.id);
        
        navigate('/'); 
      }
    } catch (err) {
      setError(err.response?.data.message || ' .');
    }
  };

  return (
    <div className="login-full-bg">
      <Container className="d-flex justify-content-center align-items-center" style={{ minHeight: '100vh' }}>
        <Card className="login-card-custom shadow-lg">
          <Card.Body className="p-5">
            <div className="text-center mb-5">
              <h2 className="login-title">REEMIN FARM</h2>
              <p className="login-subtitle">!  .</p>
            </div>

            {error && <Alert variant="danger">{error}</Alert>}

            <Form onSubmit={handleSubmit}>
              <Form.Group className="mb-4">
                <Form.Label className="fw-bold text-secondary"></Form.Label>
                <Form.Control
                  className="login-input-lg"
                  type="text" 
                  placeholder="ID"
                  value={username}
                  onChange={(e) => setUsername(e.target.value)}
                  required
                />
              </Form.Group>

              <Form.Group className="mb-5">
                <Form.Label className="fw-bold text-secondary"></Form.Label>
                <Form.Control
                  className="login-input-lg"
                  type="password"
                  placeholder="Password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  required
                />
              </Form.Group>

              <Button type="submit" className="login-btn-green w-100 py-3 mb-3">
                
              </Button>
            </Form>

            <div className="text-center mt-4">
              <button 
                className="btn btn-link text-muted small text-decoration-none me-3"
                onClick={() => navigate('/find-account')}
              >
                / 
              </button>
              <button 
                className="btn btn-link text-success fw-bold text-decoration-none"
                onClick={() => navigate('/register')}
              >
                
              </button>
            </div>
          </Card.Body>
        </Card>
      </Container>
    </div>
  );
}

export default Login;