import React, { useState } from 'react';
import { Container, Form, Button, Card, Row, Col } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { addNewProduct } from '../store.js';
import { useAuth } from "../contexts/AuthContext"; //      

function AddProduct({ fruit, setFruit }) {
    let navigate = useNavigate();
    let dispatch = useDispatch();
    const { user } = useAuth(); //      

    let [title, setTitle] = useState('');
    let [content, setContent] = useState('');
    let [price, setPrice] = useState('');
    let [imagePreview, setImagePreview] = useState(null);

    const handleImageChange = (e) => {
        const file = e.target.files[0];
        if (file) {
            const reader = new FileReader();
            reader.onloadend = () => { setImagePreview(reader.result); };
            reader.readAsDataURL(file);
        }
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        if (!title || !price || !imagePreview) return alert('    !');

        //  : Detail.js Products.js    
        const newFruit = {
            id: Date.now(),
            title: title,
            content: content,
            price: Number(price),
            imgUrl: imagePreview, //  imgUrl  ( U)
            author: user.name,    //        !
            count: 1
        };

        dispatch(addNewProduct(newFruit));
        setFruit([newFruit, ...fruit]);
        alert('  !');
        navigate('/');
    };

    return (
        <Container className="py-5" style={{ marginTop: '50px' }}>
            <Card className="border-0 shadow-lg p-4 p-md-5" style={{ borderRadius: '30px' }}>
                <h2 className="text-center fw-bold mb-5" style={{ color: '#2ecc71' }}>  </h2>
                <Form onSubmit={handleSubmit}>
                    <Row className="align-items-center">
                        <Col md={5} className="text-center mb-4 mb-md-0">
                            <div className="mb-3" style={{ width:'100%', aspectRatio:'1/1', background:'#f1f3f5', borderRadius:'20px', display:'flex', alignItems:'center', justifyContent:'center', border:'2px dashed #adb5bd', overflow:'hidden' }}>
                                {imagePreview ? <img src={imagePreview} style={{ width:'100%', height:'100%', objectFit:'cover' }} alt="" /> : <span className="text-muted"> </span>}
                            </div>
                            <Form.Label className="btn btn-outline-success w-100 rounded-pill fw-bold">
                                 PC   <Form.Control type="file" accept="image/*" hidden onChange={handleImageChange} />
                            </Form.Label>
                        </Col>
                        <Col md={7}>
                            <Form.Group className="mb-3">
                                <Form.Label className="fw-bold"></Form.Label>
                                <Form.Control placeholder="  " onChange={e => setTitle(e.target.value)} style={{ borderRadius:'12px', padding:'12px' }} />
                            </Form.Group>
                            <Form.Group className="mb-3">
                                <Form.Label className="fw-bold"> </Form.Label>
                                <Form.Control type="number" placeholder=" " onChange={e => setPrice(e.target.value)} style={{ borderRadius:'12px', padding:'12px' }} />
                            </Form.Group>
                            <Form.Group className="mb-4">
                                <Form.Label className="fw-bold"> </Form.Label>
                                <Form.Control as="textarea" rows={4} placeholder="  " onChange={e => setContent(e.target.value)} style={{ borderRadius:'12px' }} />
                            </Form.Group>
                            <Button type="submit" style={{ background:'#2ecc71', border:'none', width:'100%', padding:'15px', borderRadius:'50px', fontWeight:'800', fontSize:'1.1rem' }}>
                                  
                            </Button>
                        </Col>
                    </Row>
                </Form>
            </Card>
        </Container>
    );
}

export default AddProduct;