import React from 'react';
import { Table, Button } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { addCount, decreaseCount, deleteItem, sortName, setCheckout } from "../store.js"; 
import { useNavigate } from "react-router-dom";
import { useAuth } from '../contexts/AuthContext'; 
import './Cart.css'; 

function Cart() {
    let navigate = useNavigate();
    let dispatch = useDispatch();
    const { cart } = useSelector((state) => state); 
    const { user } = useAuth(); 
    const userName = user?.name || ''; 

    const totalSum = cart.reduce((total, item) => total + (Number(item.price) * item.count), 0);

    return (
        <div className="container cart-container">
            <div className="cart-header-text text-center">
                <h2 className="cart-title"> </h2>
                <p className="user-cart-info">   {userName} .</p>
            </div>
            
            <div className="cart-card-layout">
                <Table responsive className="cart-table">
                    <thead>
                        <tr className="text-center">
                           
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        {cart.map((item, i) => (
                            <tr key={i} className="text-center align-middle">
                               
                                <td className="product-name-td">{item.name}</td>
                                <td>
                                    <div className="qty-wrapper d-flex justify-content-center align-items-center">
                                        <button onClick={() => dispatch(decreaseCount(item.id))} className="qty-minimal-btn">-</button>
                                        <span className="qty-num mx-3">{item.count}</span>
                                        <button onClick={() => dispatch(addCount(item.id))} className="qty-minimal-btn">+</button>
                                    </div>
                                </td>
                                <td className="fw-bold">{(Number(item.price) * item.count).toLocaleString()}</td>
                                <td>
                                    <button onClick={() => dispatch(deleteItem(item.id))} className="remove-btn"></button>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </Table>

                {cart.length === 0 && (
                    <div className="text-center py-5 text-muted"> .</div>
                )}
            </div>

            <div className="cart-footer">
                <div className="row align-items-center">
                    <div className="col-md-6 mb-3 mb-md-0">
                        <button className="btn-sort-minimal" onClick={() => dispatch(sortName())}> </button>
                    </div>
                    <div className="col-md-6">
                        <div className="total-sum-box text-end">
                            <div className="total-label">  </div>
                            <div className="total-price-value">{totalSum.toLocaleString()}</div>
                            <Button 
                                className="btn-checkout-trendy mt-4" 
                                onClick={() => { 
                                    if (cart.length === 0) return alert(' .');
                                    dispatch(setCheckout(cart)); 
                                    navigate('/checkout'); 
                                }}
                            >
                                
                            </Button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Cart;